import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { useState } from 'react'
import swal from "sweetalert";
import axios, * as others from 'axios';
const Login = () => {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [error, setError] = useState(false)
  // const dispatch = useDispatch()
  const navigate = useNavigate()

  const handleLogin = async (e) => {
    e.preventDefault()
   
    // perform all neccassary validations
    if (password == "" || email == "" ) {
        alert("Please fill all fields");
    } else {
        axios.post('http://localhost:5000/auth/login', {
            email: email,
            password: password,
          }).then((res) => {
            console.log(res.data);
            if(res.status==200){
                swal({
                    text: res.data.msg,
                    icon: "success",
                    type: "success"
                  });
                  navigate("/");
            }else{
                swal({
                    text: res.data.msg,
                    icon: "error",
                    type: "error"
                  });
            }
           
            // this.props.history.push('/');
            
          }).catch((err) => {
            console.log(err);
            swal({
              text: err.response.data.errorMessage,
              icon: "error",
              type: "error"
            });
          });
    }

    

    
  }

  return (
    <>
  {/* signin-email start */}
  <section className="auth signin-email">
    <div className="page-title text-center">
      <button
        type="button"
        className="back-btn back-page-btn d-flex align-items-center justify-content-center rounded-full"
      >
        <img src="../../assets/svg/arrow-left-black.svg" alt="Icon" />
      </button>
      <h3 className="main-title">Sign In</h3>
    </div>
    <div className="auth-form">
      <form onSubmit={handleLogin}>
        <div className="d-flex flex-column gap-16">
          <div>
            <label htmlFor="lemail2">Email Address</label>
            <input
              type="email"
              id="lemail2" onChange={(e) => setEmail(e.target.value)}
              placeholder="Enter your email address"
              className="input-field d-block"
            />
          </div>
          <div>
            <label htmlFor="lpass">Password</label>
            <div className="position-relative">
              <input
                type="password"
                id="lpass"
                data-pssws-shown="false" onChange={(e) => setPassword(e.target.value)}
                placeholder="Enter your password"
                className="input-psswd input-field d-block"
              />
              <button type="button" className="eye-btn">
                <span className="eye-off">
                  <img src="../../assets/svg/eye-off.svg" alt="Eye Off" />
                </span>
                <span className="eye-on d-none">
                  <img src="../../assets/svg/eye-on.svg" alt="Eye Off" />
                </span>
              </button>
            </div>
          </div>
          <div className="d-flex align-items-center justify-content-between">
            <div className="d-flex align-items-center gap-8">
              <input
                type="checkbox"
                id="lremember"
                className="input-field-checkbox d-block"
              />
              <label htmlFor="lremember" className="checkbox-label">
                Remember Me
              </label>
            </div>
            <a
              href="forgot-password.html"
              className="fs-14 fw-600 forgot-pass d-inline-block"
            >
              Forgot Password
            </a>
          </div>
        </div>
        <button className="btn-primary">
          Sign In
        </button>
      </form>
      <div className="divider d-flex align-items-center justify-content-center gap-12">
        <span className="d-inline-block" />
        <small className="d-inline-block">Or continue with</small>
        <span className="d-inline-block" />
      </div>
      <div className="d-flex flex-column gap-16">
        <button
          type="button"
          className="social-btn"
          data-bs-toggle="modal"
          data-bs-target="#loginSuccess"
        >
          <img src="../../assets/svg/icon-google.svg" alt="Google" />
          Continue with Google
        </button>
        <button
          type="button"
          className="social-btn apple"
          data-bs-toggle="modal"
          data-bs-target="#loginSuccess"
        >
          <img src="../../assets/svg/icon-apple.svg" alt="Apple" />
          Continue with Apple
        </button>
      </div>
      <h6>
        Dont have an account? <a href="signup.html">Sign Up</a>
      </h6>
    </div>
  </section>
  {/* signin-email end */}
</>

  )
}

export default Login