import React from 'react'
import '../assets/css/auth.css'
// import { useDispatch } from 'react-redux' 
import { Link, useNavigate } from 'react-router-dom'
import { useState } from 'react'
import { register } from '../redux/authSlice'
import swal from "sweetalert";
import axios, * as others from 'axios';



const Register = () => {
  const navigate = useNavigate()
//   const dispatch = useDispatch()
  const [username, setUsername] = useState("")
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [Confirm_password, setConfirm_password] = useState("")
  const [error, setError] = useState(false)

  const handleRegister = async (e) => {
    e.preventDefault()
   
    // perform all neccassary validations
    if (password !== Confirm_password) {
        alert("Passwords don't match");
    } else {
        axios.post('http://localhost:5000/auth/register', {
            username: username,
            email: email,
            password: password,
          }).then((res) => {
            console.log(res.data);
            if(res.status==201){
                swal({
                    text: res.data.msg,
                    icon: "success",
                    type: "success"
                  });
                  navigate("/login");
            }else{
                swal({
                    text: res.data.msg,
                    icon: "error",
                    type: "error"
                  });
            }
           
            // this.props.history.push('/');
            
          }).catch((err) => {
            console.log(err);
            swal({
              text: err.response.data.errorMessage,
              icon: "error",
              type: "error"
            });
          });
    }

    

    
  }

  return (
    <>
        {/* signin start */}
        <section className="auth signin">
          <div className="heading">
            <h2>Create account</h2>
            <p>Lorem ipsum dolor sit amet</p>
          </div>
          <div className="form-area auth-form">
        <form onSubmit={handleRegister}>
          <div className="d-flex flex-column gap-16">
            <div>
              <label htmlFor="fname">Full Name</label>
              <input
                type="text" required
                id="fname"
                onChange={(e) => setUsername(e.target.value)}
                placeholder="Enter your first name"
                className="input-field d-block"
              />
            </div>
            {/* <div>
              <label htmlFor="lname">Last Name</label>
              <input
                type="text"
                id="lname"
                onChange={(e) => setEmail(e.target.value)}
                placeholder="Enter your last name"
                className="input-field d-block"
              />
            </div> */}
            <div>
              <label htmlFor="remail2">Email Address</label>
              <input
                type="email" 
                id="remail2"
                onChange={(e) => setEmail(e.target.value)}
                placeholder="Enter your email address"
                className="input-field d-block"
              />
            </div>
            <div>
              <label htmlFor="rpass">Password</label>
              <div className="position-relative">
                <input
                  type="password"
                  id="rpass" required
                  data-pssws-shown="false"
                  placeholder="Enter your password"
                  onChange={(e) => setPassword(e.target.value)}
                  className="input-psswd input-field d-block"
                />
                <button type="button" className="eye-btn">
                  <span className="eye-off">
                    <img src="../../assets/svg/eye-off.svg" alt="Eye Off" />
                  </span>
                  <span className="eye-on d-none">
                    <img src="../../assets/svg/eye-on.svg" alt="Eye Off" />
                  </span>
                </button>
              </div>
            </div>
            <div>
              <label htmlFor="rcpass">Confirm Password</label>
              <div className="position-relative">
                <input
                  type="password" required
                  id="rcpass"
                  data-pssws-shown="false"
                  onChange={(e) => setConfirm_password(e.target.value)}
                  placeholder="Confirm your password"
                  className="input-psswd input-field d-block"
                />
                <button type="button" className="eye-btn">
                  <span className="eye-off">
                    <img src="../../assets/svg/eye-off.svg" alt="Eye Off" />
                  </span>
                  <span className="eye-on d-none">
                    <img src="../../assets/svg/eye-on.svg" alt="Eye Off" />
                  </span>
                </button>
              </div>
            </div>
          </div>
          <button className="btn-primary">
            Sign Up
          </button>
        </form>
        <h6>
          Already have an account? <a href="/login">Login</a>
        </h6>
      </div>
      
        </section>
        {/* signin end */}</>

  )
}

export default Register