import React from 'react'
import  searchBar from '../assets/svg/bottom-nav/profile.svg'
import filterBlack from '../assets/svg/filter-black.svg'
import { useEffect,useSelector,useState } from 'react';
import {Link } from 'react-router-dom';
import axios, * as others from 'axios';
// home 
import "./../assets/css/home.css";

const Home = () => {
 
  const [tours, setTours] = useState([])
  useEffect(() => {
    
    const fetchTours = async() => {
      try {
        const res = axios.get(`/tour/typesData`, {
        }).then((res) => {         
          const tours =res.data  
          setTours(tours)
          
          })
      } catch (error) {
        console.error(error)
      }
    }
    fetchTours()
  }, []);

  const [types, setTypes] = useState([])
  //const {token} = useSelector((state) => state.auth)

  useEffect(() => {
    const fetchTypes = async() => {
      try {
        const res = axios.get(`/tour`, {
        }).then((res) => {         
          const types = res.data          
          setTypes(types)
        })
      } catch (error) {
        console.error(error)
      }
    }
    fetchTypes()
  }, [])



  return (
   
    <>
  {/* search start */}
  <section className="search py-12">
    <form action="#">
      <div className="form-inner w-100 d-flex align-items-center gap-8 radius-24">
        <img src={searchBar} alt="search" className="shrink-0" />
        <input
          type="search"
          className="input-search input-field"
          placeholder="Search..."
        />
        <div className="filter shrink-0">
          <button
            type="button"
            className="d-flex align-items-center"
            data-bs-toggle="modal"
            data-bs-target="#filterModal"
          >
            <img src={filterBlack} alt="filter" />
          </button>
        </div>
      </div>
    </form>
  </section>
  {/* search end */}
  
  <section className="visited py-12">
    {/* title */}
    <div className="swiper visited-swiper1">
      <div className="swiper-wrapper">
        {/* item-1 */}
        <div className="swiper-slide place-card">
          <a href="/tours">
            <div className="image position-relative">
              <img
                src="../assets/images/home/item-1.png"
                alt="desert"
                className="img-fluid w-100 overflow-hidden radius-8"
              />
              <span className="d-flex align-items-center justify-content-center rounded-full">
                <img src="../assets/svg/heart-red.svg" alt="icon" />
              </span>
            </div>
          </a>
        </div>
        {/* item-2 */}
        <div className="swiper-slide place-card">
          <a href="/tours">
            <div className="image position-relative">
              <img
                src="../assets/images/home/item-2.png"
                alt="desert"
                className="img-fluid w-100 overflow-hidden radius-8"
              />
              <span className="d-flex align-items-center justify-content-center rounded-full">
                <img src="../assets/svg/heart-black.svg" alt="icon" />
              </span>
            </div>
          </a>
        </div>
        {/* item-3 */}
        <div className="swiper-slide place-card">
          <a href="/tours">
            <div className="image position-relative">
              <img
                src="../assets/images/home/item-1.png"
                alt="desert"
                className="img-fluid w-100 overflow-hidden radius-8"
              />
              <span className="d-flex align-items-center justify-content-center rounded-full">
                <img src="../assets/svg/heart-red.svg" alt="icon" />
              </span>
            </div>
          </a>
        </div>
        {/* item-4 */}
        <div className="swiper-slide place-card">
          <a href="/tours">
            <div className="image position-relative">
              <img
                src="../assets/images/home/item-2.png"
                alt="desert"
                className="img-fluid w-100 overflow-hidden radius-8"
              />
              <span className="d-flex align-items-center justify-content-center rounded-full">
                <img src="../assets/svg/heart-black.svg" alt="icon" />
              </span>
            </div>
          </a>
        </div>
      </div>
    </div>
  </section>
  {/* visited start */}
  <section className="guide py-12">
  <div class="title d-flex align-items-center justify-content-between">
        <h2 class="shrink-0">Frequently visited</h2>
        <a href="tour-guide.html" class="shrink-0 d-inline-block">See All</a>
      </div>

{/* cards */}
<div className="d-flex gap-16 all-cards scrollbar-hidden">
    {/* item 1 */}
    {tours.map((details) => (
    <Link to={`/list/${details._id}`} 
      href="profile/guide-profile.html"
      className=" gap-24 item w-fit shrink-0"
    >
   <div className="image position-relative">
    <img
      src="../assets/images/home/item-1.png"
      alt="desert"
      className="img-fluid w-100 overflow-hidden radius-8"
    />
    {/* <span className="d-flex align-items-center justify-content-center rounded-full">
      <img src="../assets/svg/heart-red.svg" alt="icon" />
    </span> */}
  </div>
  <div className="content">
    <h4>{details.type}({details.countdata})</h4>
    <p className="d-flex align-items-center gap-04 location">
      <img src="../assets/svg/map-marker.svg" alt="icon" />
      Polynesia, French
    </p>

  </div>
      </Link>
  ))}
  </div>

  
  </section>
  {/* visited end */}
  {/* budget start */}
  <section className="budget pt-12">
    {/* title */}
    <div className="title d-flex align-items-center justify-content-between">
      <h2 className="shrink-0">On Budget Tour</h2>
      <a href="/tours" className="shrink-0 d-inline-block">
        See All
      </a>
    </div>
    <ul>
      {/* item 1 */}
      {types.map((value) => (
      <li>
       <Link to={`/tourDetails/${value._id}`} 
          className="d-flex align-items-center gap-12"
        >
          <div className="image shrink-0 overflow-hidden radius-8">
            <img
              src="../assets/images/home/budget-1.png"
              alt="Place"
              className="img-fluid w-100 h-100 object-fit-cover"
            />
          </div>
          <div className="content shrink-0 d-flex align-items-center gap-12 justify-content-between flex-grow">
            <div>
              <h4>{value.title}</h4>
              <h5>{value.packageTime}</h5>
              <p className="d-flex align-items-center gap-8 location">
                <img src="../assets/svg/map-marker.svg" alt="icon" />
                {value.place}
              </p>
            </div>
            <p className="price">
              <span>{value.price}</span>/Person
            </p>
          </div>
        </Link>
      </li>
    ))}
    </ul>
  </section>
  {/* budget end */}
</>


  )
}

export default Home