import React from 'react'
import {Link } from 'react-router-dom';
import burgerWhite from '../../assets/svg/menu/burger-white.svg'
import sunWhite from '../../assets/svg/menu/sun-white.svg'
import moonWhite from '../../assets/svg/menu/moon-white.svg'
import pieWhite from '../../assets/svg/menu/pie-white.svg'
import chevronRightBlack from '../../assets/svg/menu/chevron-right-black.svg'
import pageWhite from "../../assets/svg/menu/page-white.svg"
import closeWhite from '../../assets/svg/menu/close-white.svg'
import avatar from '../../assets/images/profile/avatar.png'
import profileWhite from '../../assets/svg/menu/profile-white.svg'
const Header = () => {
 
  
 
  return (
    <>
  {/* menu, side-menu start */}
  <section className="wrapper dz-mode">
    {/* menu */}
    <div className="menu">
      <button className="toggle-btn">
        <img src={burgerWhite}
          alt=""
          className="icon"
        />
      </button>
      <div className="btn-grp d-flex align-items-center gap-16">
        <label
          htmlFor="mode-change"
          className="mode-change d-flex align-items-center justify-content-center"
        >
          <input type="checkbox" id="mode-change" />
          <img
            src={sunWhite}
            alt="icon"
            className="sun"
          />
          <img
            src={moonWhite}
            alt="icon"
            className="moon"
          />
        </label>
        <Link to="profile/user-profile.html">
          <img src={profileWhite} alt="icon" />
        </Link>
      </div>
    </div>
    <div className="m-menu__overlay" />
    {/* main menu */}
    <div className="m-menu">
      <div className="m-menu__header">
        <button className="m-menu__close">
          <img src={closeWhite} alt="icon" />
        </button>
        <div className="menu-user">
          <img src={avatar} alt="avatar" />
          <div>
            <Link to="#!">Thai5.Tours</Link>
            <h3>Verified user  Membership</h3>
          </div>
        </div>
      </div>
      <ul>
        <li>
          <h2 className="menu-title">menu</h2>
        </li>
        <li>
          <Link  to="/home">
            <div className="d-flex align-items-center gap-16">
              <span className="icon">
                <img src={pieWhite} alt="" />
              </span>
              Home
            </div>
            <img src={chevronRightBlack} alt="" />
          </Link>
        </li>
        <li>
          <Link to="/Packages">
            <div className="d-flex align-items-center gap-16">
              <span className="icon">
                <img src={pageWhite} alt="" />
              </span>
              Packages
            </div>
            <img src={chevronRightBlack} alt="" />
          </Link>
        </li>
        <li>
          <Link to="/tours">
            <div className="d-flex align-items-center gap-16">
              <span className="icon">
                <img src={pageWhite} alt="" />
              </span>
              tours
            </div>
            <img src={chevronRightBlack} alt="" />
          </Link>
        </li>
        <li>
          <h2 className="menu-title">others</h2>
        </li>
     
        <li>
          <label className="a-label__chevron" htmlFor="item-5">
            <span className="d-flex align-items-center gap-16">
              <span className="icon">
                <img src="../assets/svg/menu/gear-white.svg" alt="" />
              </span>
              Details
            </span>
            <img src="../assets/svg/menu/chevron-right-black.svg" alt="" />
          </label>
          <input
            type="checkbox"
            id="item-5"
            name="item-5"
            className="m-menu__checkbox"
          />
          <div className="m-menu">
            <div className="m-menu__header">
              <label className="m-menu__toggle" htmlFor="item-5">
                <img src="../assets/svg/menu/back-white.svg" alt="" />
              </label>
              <span className="m-menu__header-title">settings</span>
            </div>
            <ul>
              <li>
                <Link to="profile/user-address.html">
                  <div className="d-flex align-items-center gap-16">
                    <span className="icon">
                      <img src="../assets/svg/menu/box-white.svg" alt="icon" />
                    </span>
                    My Address
                  </div>
                </Link>
              </li>
              <li>
                <Link to="profile/user-payment.html">
                  <div className="d-flex align-items-center gap-16">
                    <span className="icon">
                      <img src="../assets/svg/menu/box-white.svg" alt="icon" />
                    </span>
                    Payment Method
                  </div>
                </Link>
              </li>
              <li>
                <Link to="profile/change-password.html">
                  <div className="d-flex align-items-center gap-16">
                    <span className="icon">
                      <img src="../assets/svg/menu/box-white.svg" alt="icon" />
                    </span>
                    Change Password
                  </div>
                </Link>
              </li>
              <li>
                <Link to="profile/forgot-password.html">
                  <div className="d-flex align-items-center gap-16">
                    <span className="icon">
                      <img src="../assets/svg/menu/box-white.svg" alt="icon" />
                    </span>
                    Forgot Password
                  </div>
                </Link>
              </li>
              <li>
                <Link to="profile/security.html">
                  <div className="d-flex align-items-center gap-16">
                    <span className="icon">
                      <img src="../assets/svg/menu/box-white.svg" alt="icon" />
                    </span>
                    Security
                  </div>
                </Link>
              </li>
              <li>
                <Link to="profile/user-language.html">
                  <div className="d-flex align-items-center gap-16">
                    <span className="icon">
                      <img src="../assets/svg/menu/box-white.svg" alt="icon" />
                    </span>
                    Language
                  </div>
                </Link>
              </li>
              <li>
                <Link to="profile/notifications.html">
                  <div className="d-flex align-items-center gap-16">
                    <span className="icon">
                      <img src="../assets/svg/menu/box-white.svg" alt="icon" />
                    </span>
                    Notifications
                  </div>
                </Link>
              </li>
            </ul>
          </div>
        </li>
        <li>
          <Link to="/login">
            <div className="d-flex align-items-center gap-16">
              <span className="icon">
                <img src={pageWhite} alt="" />
              </span>
              Login
            </div>
            <img src={chevronRightBlack} alt="" />
          </Link>
        </li>
        <input type="hidden" id="enableMode" className="mode-switch" />
      </ul>
    </div>
    {/* end main menu */}
  </section>
  {/* menu, side-menu end */}
</>

  )
}

export default Header