import React from 'react'
import {Routes,Route,Navigate} from 'react-router-dom'
import Home from '../Pages/Home'
import ChangePassword from '../Pages/ChangePassword'
import Login from '../Pages/Login'
import PrivacyPolicy from '../Pages/PrivacyPolicy'
import Profile from '../Pages/Profile'
import Register from '../Pages/Register'
import Terms from '../Pages/Terms&Condition'
import TicketBooked from '../Pages/TicketBooked'
import TourDetails from '../Pages/TourDetails'
import Tours from '../Pages/Tours'
import Packages from '../Pages/Packages'
import List from '../Pages/SearchResults'
import WishList from '../Pages/WishList'

const Routers = () => {
  return (
    <Routes>
      <Route path='/' element={<Navigate to ='/home'/>}/>
      <Route path='/home' element={<Home />}/>
      <Route path='/changePassword' element={<ChangePassword />}/>
      <Route path='/login' element={<Login />}/>
      <Route path='/privacyPolicy' element={<PrivacyPolicy />}/>
      <Route path='/profile' element={<Profile />}/>
      <Route path='/register' element={<Register />}/>
      <Route path='/terms' element={<Terms />}/>
      <Route path='/ticketBooked' element={<TicketBooked />}/>
      <Route path='/Packages' element={<Packages />}/>
      <Route path='/tours' element={<Tours />}/>
      <Route path='/tourDetails/:id' element={<TourDetails />}></Route>
      <Route path='/list/:id' element={<List />}/>
      <Route path='/wishList'  element={<WishList />}/>
    
    </Routes>
  )
}

export default Routers