import React from 'react'
import { useEffect,useState } from 'react';
import Portal from 'react-overlays/Portal';
import {Link,useParams } from 'react-router-dom';
import axios, * as others from 'axios';
import "./../assets/css/details.css"

const TourDetails = () => {
  const [imageIndex, setImageIndex] = useState(0);

  const handleClick = () => {
    setImageIndex(prevImageIndex => (prevImageIndex + 1) % 2);
  };

  const images = [
    '../assets/svg/heart-black.svg',
    '../assets/svg/heart-red-light.svg',
  ];
  const [types, setTypes] = useState([])
  //const {token} = useSelector((state) => state.auth)
  const { id } = useParams()
  useEffect(() => {
    const fetchTypes = async() => {
      try {
        const res = axios.get(`http://localhost:5000/tour/details/${id}`, {
        }).then((res) => {         
          const types = (res.data)         
          setTypes(types)
        })
      } catch (error) {
        console.error(error)
      }
    }
    fetchTypes()
  }, [])
  return (

<>
  {/* banner start */}
  <section className="banner position-relative">
    <img
      src="../assets/images/details/banner-1.png"
      alt="Banner"
      className="w-100 img-fluid"
    />
    {/* title */}
    <div className="page-title">
      <button
        type="button"
        className="back-btn back-page-btn d-flex align-items-center justify-content-center rounded-full"
      >
        <img src="../assets/svg/arrow-left-black.svg" alt="arrow" />
      </button>
      <h3 className="main-title">Vacation Details</h3>
    </div>
  </section>
  {/* banner end */}
  {/* details-body start */}
  <section className="details-body">
    {/* details-title */}
    <section className="d-flex align-items-center gap-8 details-title">
      <div className="flex-grow">
        <h3>{types.title}</h3>
        <ul className="d-flex align-items-center gap-8">
          <li className="d-flex align-items-center gap-04">
            <img src="../assets/svg/map-marker.svg" alt="icon" />
            <p>{types.place}</p>
          </li>
          <li className="d-flex align-items-center gap-04">
            <img src="../assets/svg/star-yellow.svg" alt="icon" />
            <p>
              <span>4.4</span> (41 Reviews)
            </p>
          </li>
        </ul>
      </div>
      <span className="d-flex align-items-center justify-content-center rounded-full shrink-0">
       
        <img src={images[imageIndex]} alt="Image" onClick={handleClick} />
  

      </span>
    </section>
    {/* details-info */}
    <section className="details-info pt-32 pb-16">
      <div className="title">
        <h4>Details</h4>
      </div>
      <p>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Tortor ac leo
        lorem nisl. Viverra vulputate sodales quis et dui, lacus. Iaculis eu
        egestas leo egestas vel. Ultrices ut magna nulla facilisi commodo enim,
        orci feugiat pharetra. Id sagittis, ullamcorper turpis ultrices platea
        pharetra.
      </p>
      <ul>
        <li><b>Timeing</b>:{types.packageTime}</li>
        
      </ul>
    </section>
  

    {/* location start */}
    <section className="details-location pt-16">
      {/* title */}
      <div className="title">
        <h4>Faq</h4>
      </div>
      {/* map */}
   
      <section className="faq px-24 py-32">
    <div className="accordion" id="accordionExample">
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseOne"
            aria-expanded="true"
            aria-controls="collapseOne"
          >
            Lorem ipsum dolor sit amet
          </button>
        </h2>
        <div
          id="collapseOne"
          className="accordion-collapse collapse show"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body">
            <p>
              {types.Answer1}
            </p>
          </div>
        </div>
      </div>
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseTwo"
            aria-expanded="false"
            aria-controls="collapseTwo"
          >
            Lorem ipsum dolor sit amet
          </button>
        </h2>
        <div
          id="collapseTwo"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body">
            <p>
            {types.Answer2}
            </p>
          </div>
        </div>
      </div>
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseThree"
            aria-expanded="false"
            aria-controls="collapseThree"
          >
            Lorem ipsum dolor sit amet
          </button>
        </h2>
        <div
          id="collapseThree"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body">
            <p>
            {types.Answer3}
            </p>
          </div>
        </div>
      </div>
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseFour"
            aria-expanded="false"
            aria-controls="collapseFour"
          >
            Lorem ipsum dolor sit amet
          </button>
        </h2>
        <div
          id="collapseFour"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body">
            <p>
             {types.Answer4}
            </p>
          </div>
        </div>
      </div>
      <div className="accordion-item">
        <h2 className="accordion-header">
          <button
            className="accordion-button collapsed"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseFive"
            aria-expanded="false"
            aria-controls="collapseFive"
          >
            Lorem ipsum dolor sit amet
          </button>
        </h2>
        <div
          id="collapseFive"
          className="accordion-collapse collapse"
          data-bs-parent="#accordionExample"
        >
          <div className="accordion-body">
            <p>
            {types.Answer5}
            </p>
          </div>
        </div>
      </div>
    </div>
    </section>
   
    </section>
    {/* location end */}
  </section>
  {/* details-body end */}
  {/* details-footer start */}
  <section className="details-footer d-flex align-items-center justify-content-between gap-8 w-100">
    <p>
      ${types.price} <span>/Person</span>
    </p>
    <a href="/ticketBooked">Book Now</a>
  </section>
  {/* details-footer end */}
</>

  )
}

export default TourDetails