import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import Header from './../Header/Header'
import Routers from './../../router/Routers'
import Footer from './../Footer/Footer'

const Layout = () => {  
   
  const location=useLocation();
  let item;
  const shouldHideFooter = location.pathname.includes('tourDetails');

  switch(true){
    case location.pathname.includes('tourDetails'):
    item='details vacation-details';
    break;
    case location.pathname.includes('ticketBooked'):
    item='booking-main checkout-vacation';
    break;
    default:
      item="home"

  }



 

  return (
    
    <>
    
     <main className={item} >
    <Header/>
    <Routers/>
    </main>
    {!shouldHideFooter && <Footer />}

    </>
  )
}

export default Layout